
.header {
    background-color: #333; /* Dark background color */
    color: #fff; /* White text color */
    padding: 20px; /* Add some padding for spacing */
   height: auto;
   max-width: 100vw;
  }
  
  .image {
    transition: transform 0.5s ease; /* Add a transition for smoother effect */
  }

  
  
  .image:hover {
    transform: perspective(500px) rotateY(10deg); /* Apply 3D transformation on hover */ 
  }
  .nav{
    display: flex;
  }
  .nav a{
    color: #fff; /* White text color for links */
    text-decoration: none; /* Remove default underline */

  }
  
  .nav a:hover {
    color: #ffd700; /* Change text color on hover to gold */
  }
  
  .nav h1 {
    font-size: clamp(6px,2vw,30px); /* Adjust font size */
    margin: 0; /* Remove default margins */
    cursor: pointer; /* Change cursor to pointer on hover */
  }
  


.homeCoin {
    background-color: #1a1a1a; /* Dark background */
    color: #ffffff; /* White text color */
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.container-main {
    display: flex;
    justify-content: start;
    align-items: center;
 
}

.left-column {
    flex: 1;
    padding: 2rem;
    color: #ffffff; /* White text color */
}

.right-column {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.name {
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 1rem;
}

.title {
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 1.5rem;
}

.description {
    font-size: 1rem;
    margin-bottom: 2rem;
    line-height: 1.5;
}

.btn {
    padding: 0.75rem 2rem;
    font-size: 1rem;
    font-weight: bold;
    text-transform: uppercase;
    border: none;
    border-radius: 0.5rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.btn-primary {
    background-color: #007bff; /* Blue button color */
    color: #ffffff;
}

.btn-primary:hover {
    background-color: #0056b3; /* Darker blue on hover */
}

.img-fluid {
    max-width: 100%;
    height: auto;
    border-radius: 0.5rem;
    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.3); /* Soft shadow effect */
    transition: transform 0.3s ease;
}



@media (max-width: 768px) {
    .container-main {
        flex-direction: column;
    }

    .left-column,
    .right-column {
        width: 100%;
        text-align: center;
        color: #ffffff; /* White text color */
    }
}


@keyframes typing {
    from { width: 0 }
    to { width: 100% }
}

.typing-animation {
    display: inline-block;
    font-size: clamp(10px,6vw,30px);
    overflow: hidden;
    border-right: 0.15em solid white;
    font-weight: 800;
    color: aquamarine; /* Adjust thickness and color to match your design */
    white-space: nowrap;
    margin: 0;
    letter-spacing: 0.15em; /* Adjust spacing to match your design */
    animation: typing 3s steps(30, end); /* Adjust duration as needed */
}

