.workCoin {
    background-color: rgb(25, 25, 25);
    padding: 2rem; /* Add padding for better spacing */
}


.boxCoin {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between; /* Distribute space between items */
    background-color: rgb(17, 16, 16);
    color: white;
    border: 2px solid rgb(9, 9, 9);
}

.projectBox {
    flex: 0 0 calc(30.33% - 20px); /* Adjust width to fit three items in one line */
    margin-bottom: 60px; /* Add margin between boxes */
border: 2px solid rgb(15, 14, 14);
background-color: white;
border-radius: 10px;
box-shadow: .2rem 0.5rem 1rem .6rem rgba(211, 203, 203, 0.187);
padding-bottom: 1rem;
}




.projectBox:hover {
    transform: translateY(-5px);
    box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.1);
}

.projectBox img {
    width: 100%;
    height: auto;
    border-top-left-radius: 0.5rem;
    border-top-right-radius: 0.5rem;
    transition: transform 0.3s ease; /* Add transition for image hover */
}

.projectBox img:hover {
    transform: scale(1.05); /* Increase scale on hover */
}

.projectBox h1 {
    font-size: 1.6rem;
    color: #333;
    font-family: 'Times New Roman', Times, serif;
    font-weight: 700;
    padding: 1rem; /* Add padding for better spacing */
    text-align: center;
}

.projectBox h2 {
    font-size: 1.3rem;
    color: #666;
    margin: 0.5rem; /* Add margin for better spacing */
    text-align: center;
}

.projectBox a {
    display: block;
    padding: 0.5rem 1rem;
    background-color: #5079a56d;
    color: white;
    text-decoration: none;
    text-align: center;
    border-radius: 0.3rem;
    font-weight: 800;
    transition: background-color 0.3s ease;
}

.projectBox a:hover {
    background-color: #0056b3;
}
